import { Component, OnInit, Input } from '@angular/core';
import { Book } from '../../shared/book.model';
import { BookService } from '../book.service';
import { ActivatedRoute, Params, Router} from '@angular/router';
import { ShoppingListService } from '../../shopping-list/shopping-list.service';
import { map } from 'rxjs/operators';
import { faShoppingCart, faShare, faCogs, faEdit, faTrashAlt, faCaretUp, faCaretDown } from '@fortawesome/free-solid-svg-icons';


@Component({
  selector: 'app-book-detail',
  templateUrl: './book-detail.component.html',
  styleUrls: ['./book-detail.component.css']
})
export class BookDetailComponent implements OnInit {
	  book: Book;
    isCollapsed = false;
    id: number;
    faShoppingCart = faShoppingCart;
    faShare = faShare;
    faCogs = faCogs;
    faEdit = faEdit;
    faTrashAlt = faTrashAlt;
    faCaretUp = faCaretUp;
    faCaretDown = faCaretDown;
  

  constructor(private bookService: BookService, 
    private slService: ShoppingListService,
    private route: ActivatedRoute,
    private router: Router) { }

  ngOnInit() {
    // fontawesome.library.add(faShoppingCart, faCaretDown, faCaretUp);

    this.route.params
      .subscribe((params: Params) => {
        this.id = +params['id'];

            this.book = this.bookService.getBook(this.id);
                 // this.book = this.slService.getBook(this.id);


      });


  }


  onAddToShoppingList(){
  	this.bookService.addBookToShoppingList(this.book)
  }


   onEditBook() {
    this.router.navigate(['edit'], {relativeTo: this.route});
    // this.router.navigate(['../', this.id, 'edit'], {relativeTo: this.route});
  }

    onDeleteBook() {
    this.bookService.deleteBook(this.id);
    this.router.navigate(['/books']);
  }



}
